import * as React from "react"
import { graphql, Link, PageProps } from "gatsby"

import Bio from "../components/bio"
import Seo from "../components/seo"
import Layout from "../components/layout"
import { Post } from "../models/post"
import extractCategoryFromSlug from "../utils/extract-category-from-slug"

export type DataProps = {
  site: {
    siteMetadata: {
      title: string
    }
  }
  allMarkdownRemark: {
    nodes: Post[]
  }
}

type ContextProps = {
  categoryFolder?: string
}

const PostsList: React.FC<PageProps<DataProps, ContextProps>> = ({ pageContext, data, location }) => {
  const {
    site: { siteMetadata },
    allMarkdownRemark: { nodes: posts },
  } = data

  const siteTitle = siteMetadata?.title

  if (!siteTitle) {
    throw new Error("A title is mandatory for every post")
  }

  return (
    <Layout location={location} title={siteTitle}>
      <Bio />
      <ol style={{ listStyle: `none` }}>
        {posts.filter(post => !post.frontmatter.draft)
          .map(post => {
            const title = post.frontmatter.title || post.fields.slug
            const category = extractCategoryFromSlug(post.fields.slug)

            return (
              <li key={post.fields.slug}>
                <article
                  className="post-list-item"
                  itemScope
                  itemType="http://schema.org/Article"
                >
                  <header>
                    {
                      !pageContext.categoryFolder 
                      || !pageContext.categoryFolder.includes(category)
                        ? <Link to={category} className="filter category">
                          {category}
                        </Link>
                        : null
                    }
                    <h2>
                      <Link to={post.fields.slug} itemProp="url">
                        <span itemProp="headline">{title}</span>
                      </Link>
                    </h2>
                    <small>{post.frontmatter.date}</small>
                  </header>
                  <section>
                    <p
                      dangerouslySetInnerHTML={{
                        __html: post.frontmatter.description || post.excerpt,
                      }}
                      itemProp="description"
                    />
                  </section>
                </article>
              </li>
            )
          })}
      </ol>
    </Layout>
  )
}

export default PostsList

export const Head = () => <Seo title="Tots els articles" />

export const pageQuery = graphql`
  query PostsList(
    $categoryFolder: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      filter: { fields: { slug: { regex: $categoryFolder } } }, 
      sort: { frontmatter: { date: DESC } }
    ) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "DD MMMM YYYY", locale: "ca")
          title
          description
          draft
        }
      }
    }
  }
`